.progress-header {
    background: linear-gradient(135deg, rgba(26, 0, 55, 0.3) 0%, rgba(0, 0, 0, 0.5) 100%);
    padding: 28px;
    border-radius: 20px;
    margin-bottom: 30px;
    box-shadow: 0 8px 32px rgba(26, 0, 55, 0.6),
                0 0 20px rgba(33, 165, 185, 0.2),
                inset 0 1px 0 rgba(255, 255, 255, 0.1);
    border: 1px solid rgba(33, 165, 185, 0.2);
    max-width: 800px;
    margin-left: auto;
    margin-right: auto;
}

.progress-header h2 {
    font-size: 32px;
    font-weight: bold;
    margin-bottom: 20px;
    text-align: center;
    background: linear-gradient(135deg, #fff 0%, #21a5b9 50%, #758228 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    background-color: transparent;
}

.progress-stats {
    display: flex;
    justify-content: space-between;
    margin-bottom: 20px;
    font-weight: 500;
    color: rgba(255, 255, 255, 0.9);
    background: transparent;
    flex-wrap: wrap;
    gap: 10px;
}

.progress-stats span {
    background: rgba(33, 165, 185, 0.1);
    padding: 10px 16px;
    border-radius: 12px;
    border: 1px solid rgba(33, 165, 185, 0.2);
    font-size: 14px;
    letter-spacing: 0.5px;
}

.progress-container {
    width: 100%;
    height: 24px;
    background: rgba(26, 0, 55, 0.4);
    border-radius: 12px;
    overflow: hidden;
    box-shadow: inset 0 2px 4px rgba(0, 0, 0, 0.3);
    border: 1px solid rgba(33, 165, 185, 0.2);
}

.progress-bar {
    height: 100%;
    background: linear-gradient(135deg, #21a5b9 0%, #758228 100%);
    transition: width 1s ease-in-out;
    box-shadow: 0 0 20px rgba(33, 165, 185, 0.6),
                inset 0 1px 0 rgba(255, 255, 255, 0.2);
    position: relative;
    overflow: hidden;
}

.progress-bar::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: linear-gradient(90deg, 
        transparent 0%, 
        rgba(255, 255, 255, 0.3) 50%, 
        transparent 100%);
    animation: shimmer 2s infinite;
}

@keyframes shimmer {
    0% {
        transform: translateX(-100%);
    }
    100% {
        transform: translateX(100%);
    }
}

@media (max-width: 640px) {
    .progress-header {
        padding: 20px;
    }

    .progress-header h2 {
        font-size: 24px;
    }

    .progress-stats {
        flex-direction: column;
        gap: 8px;
    }

    .progress-stats span {
        width: 100%;
        text-align: center;
    }
}
