.technology-card {
    position: relative;
    border: 2px solid rgba(33, 165, 185, 0.3);
    border-radius: 20px;
    padding: 28px;
    margin: 10px;
    background: linear-gradient(135deg, rgba(26, 0, 55, 0.3) 0%, rgba(0, 0, 0, 0.5) 100%);
    transition: all 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275);
    box-shadow: 0 4px 20px rgba(26, 0, 55, 0.4),
                0 0 20px rgba(33, 165, 185, 0.1);
    max-width: 300px;
    cursor: pointer;
    overflow: hidden;
    backdrop-filter: blur(10px);
    -webkit-backdrop-filter: blur(10px);
}

.technology-card::before {
    content: '';
    position: absolute;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100%;
    background: linear-gradient(90deg, transparent, rgba(33, 165, 185, 0.2), transparent);
    transition: left 0.6s;
    z-index: 0;
}

.technology-card:hover::before {
    left: 100%;
}

.technology-card:hover {
    transform: translateY(-10px) scale(1.02);
    box-shadow: 0 15px 40px rgba(33, 165, 185, 0.4),
                0 0 30px rgba(117, 130, 40, 0.3);
    border-color: rgba(33, 165, 185, 0.6);
    background: linear-gradient(135deg, rgba(26, 0, 55, 0.5) 0%, rgba(0, 0, 0, 0.7) 100%);
}

.technology-card.not-started {
    border-color: rgba(255, 107, 107, 0.4);
    box-shadow: 0 4px 20px rgba(255, 107, 107, 0.2),
                0 0 20px rgba(255, 107, 107, 0.1);
}

.technology-card.not-started:hover {
    border-color: rgba(255, 107, 107, 0.6);
    box-shadow: 0 15px 40px rgba(255, 107, 107, 0.4),
                0 0 30px rgba(255, 107, 107, 0.3);
}

.technology-card.in-progress {
    border-color: rgba(33, 165, 185, 0.4);
    box-shadow: 0 4px 20px rgba(33, 165, 185, 0.2),
                0 0 20px rgba(33, 165, 185, 0.1);
}

.technology-card.in-progress:hover {
    border-color: rgba(33, 165, 185, 0.6);
    box-shadow: 0 15px 40px rgba(33, 165, 185, 0.4),
                0 0 30px rgba(33, 165, 185, 0.3);
}

.technology-card.completed {
    border-color: rgba(117, 130, 40, 0.4);
    box-shadow: 0 4px 20px rgba(117, 130, 40, 0.2),
                0 0 20px rgba(117, 130, 40, 0.1);
}

.technology-card.completed:hover {
    border-color: rgba(117, 130, 40, 0.6);
    box-shadow: 0 15px 40px rgba(117, 130, 40, 0.4),
                0 0 30px rgba(117, 130, 40, 0.3);
}

.card-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
    position: relative;
    z-index: 1;
}

.card-header h3 {
    text-align: center;
    color: #ffffff;
    font-size: 22px;
    font-weight: 700;
    background: transparent;
    flex: 1;
    background: linear-gradient(135deg, #fff 0%, #21a5b9 50%, #758228 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
}

.status-icon {
    font-size: 1rem;
    margin: 10px;
    text-align: center;
    text-decoration: underline;
    text-decoration-color: #21a5b9;
    color: rgba(255, 255, 255, 0.8);
    background: transparent;
    position: relative;
    z-index: 1;
    font-weight: 500;
    
}

.card-description {
    color: rgba(255, 255, 255, 0.85);
    font-size: 1rem;
    line-height: 2;
    background: transparent;
    position: relative;
    z-index: 1;
    padding: 12px;
    border-radius: 8px;
    background: rgba(33, 165, 185, 0.05);
    border-left: 3px solid rgba(33, 165, 185, 0.3);
    
}



@media (max-width: 640px) {
    .technology-card {
        max-width: 100%;
        padding: 20px;
    }

    .card-header h3 {
        font-size: 18px;
    }
}
