.App {
    text-align: center;
    background: var(--back-color);
    min-height: 100vh;
    padding: 20px;
}

.App h1 {
    font-size: 48px;
    font-weight: bold;
    margin-bottom: 40px;
    background: linear-gradient(135deg, #fff 0%, #21a5b9 50%, #758228 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-shadow: 0 0 40px rgba(33, 165, 185, 0.5);

}

@keyframes titleGlow {
    0%, 100% {
        filter: brightness(1);
    }
    50% {
        filter: brightness(1.2);
    }
}

.filters {
    margin: 20px auto;
    padding: 28px;
    background: linear-gradient(135deg, rgba(26, 0, 55, 0.3) 0%, rgba(0, 0, 0, 0.5) 100%);
    border-radius: 20px;
    max-width: 800px;
    box-shadow: 0 8px 32px rgba(26, 0, 55, 0.6),
                0 0 20px rgba(33, 165, 185, 0.2),
                inset 0 1px 0 rgba(255, 255, 255, 0.1);
    border: 1px solid rgba(33, 165, 185, 0.2);
}

.filters h3 {
    margin-top: 0;
    margin-bottom: 20px;
    color: rgba(255, 255, 255, 0.9);
    text-align: center;
    font-size: 24px;
    font-weight: 600;
    background: transparent;
}

.filter-buttons {
    display: flex;
    gap: 12px;
    flex-wrap: wrap;
    justify-content: center;
}

.filter-btn {
    padding: 12px 24px;
    font-size: 14px;
    font-weight: 500;
    border: 2px solid rgba(33, 165, 185, 0.3);
    border-radius: 30px;
    background: rgba(26, 0, 55, 0.3);
    color: rgba(255, 255, 255, 0.9);
    cursor: pointer;
    transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
    letter-spacing: 0.5px;
    position: relative;
    overflow: hidden;
}

.filter-btn::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(33, 165, 185, 0.4);
    opacity: 0;
    transition: opacity 0.3s ease;
    border-radius: 30px;
    z-index: -1;
}

.filter-btn:hover {
    color: #ffffff;
    transform: translateY(-2px);
    box-shadow: 0 4px 15px rgba(33, 165, 185, 0.4);
    border-color: rgba(33, 165, 185, 0.5);
}

.filter-btn:hover::before {
    opacity: 1;
}

.filter-btn.active {
    background: linear-gradient(135deg, rgba(33, 165, 185, 0.4) 0%, rgba(117, 130, 40, 0.4) 100%);
    color: #ffffff;
    border-color: rgba(33, 165, 185, 0.6);
    font-weight: bold;
    box-shadow: 0 4px 15px rgba(33, 165, 185, 0.5);
}

.filter-btn.active::before {
    opacity: 1;
}

/* Стили для списка технологий */
.tech-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 24px;
    padding: 20px;
    max-width: 1280px;
    margin: 0 auto;
}

@media (max-width: 768px) {
    .App h1 {
        font-size: 36px;
    }

    .filters {
        padding: 20px;
    }

    .filters h3 {
        font-size: 20px;
    }

    .filter-btn {
        padding: 10px 20px;
        font-size: 13px;
    }
}

@media (max-width: 480px) {
    .App h1 {
        font-size: 28px;
    }

    .filters {
        padding: 16px;
    }

    .filter-buttons {
        gap: 8px;
    }

    .filter-btn {
        padding: 8px 16px;
        font-size: 12px;
    }
}
