.quick-actions {
    margin: 20px auto;
    padding: 28px;
    background: linear-gradient(135deg, rgba(26, 0, 55, 0.3) 0%, rgba(0, 0, 0, 0.5) 100%);
    border-radius: 20px;
    max-width: 800px;
    box-shadow: 0 8px 32px rgba(26, 0, 55, 0.6),
                0 0 20px rgba(33, 165, 185, 0.2),
                inset 0 1px 0 rgba(255, 255, 255, 0.1);
    border: 1px solid rgba(33, 165, 185, 0.2);
}

.quick-actions h3 {
    margin-top: 0;
    margin-bottom: 20px;
    color: rgba(255, 255, 255, 0.9);
    text-align: center;
    font-size: 24px;
    font-weight: 600;
    background: transparent;
    background: linear-gradient(135deg, #fff 0%, #21a5b9 50%, #758228 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
}

.actions-buttons {
    display: flex;
    gap: 15px;
    flex-wrap: wrap;
    justify-content: center;
}

.action-btn {
    padding: 14px 28px;
    font-size: 14px;
    font-weight: 500;
    border: 2px solid transparent;
    border-radius: 30px;
    cursor: pointer;
    transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
    flex: 1;
    min-width: 200px;
    letter-spacing: 0.5px;
    position: relative;
    overflow: hidden;
}

.action-btn::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    transition: opacity 0.3s ease;
    border-radius: 30px;
    z-index: 0;
}

.action-btn:hover {
    transform: translateY(-2x) scale(1.05);
    box-shadow: 0 2px 15px rgba(0, 0, 0, 0.4);
}

.action-btn:active {
    transform: translateY(-2px) scale(1.02);
}

.action-btn span {
    position: relative;
    z-index: 1;
}

.complete-all {
    background: linear-gradient(60deg, rgba(117, 130, 40, 0.3) 0%, rgba(117, 130, 40, 0.5) 100%);
    color: white;
    border-color: rgba(117, 130, 40, 0.4);
}

.complete-all::before {
    background: linear-gradient(60deg, rgba(117, 130, 40, 0.5) 0%, rgba(117, 130, 40, 0.7) 100%);
}

.complete-all:hover {
    background: linear-gradient(60deg, rgba(117, 130, 40, 0.5) 0%, rgba(117, 130, 40, 0.7) 100%);
    border-color: rgba(117, 130, 40, 0.6);
    box-shadow: 0 15px 40px rgba(117, 130, 40, 0.5);
}

.complete-all:hover::before {
    opacity: 1;
}

.reset-all {
    background: linear-gradient(60deg, rgba(255, 107, 107, 0.3) 0%, rgba(255, 107, 107, 0.5) 100%);
    color: white;
    border-color: rgba(255, 107, 107, 0.4);
}

.reset-all::before {
    background: linear-gradient(60deg, rgba(255, 107, 107, 0.5) 0%, rgba(255, 107, 107, 0.7) 100%);
}

.reset-all:hover {
    background: linear-gradient(60deg, rgba(255, 107, 107, 0.5) 0%, rgba(255, 107, 107, 0.7) 100%);
    border-color: rgba(255, 107, 107, 0.6);
    box-shadow: 0 15px 40px rgba(255, 107, 107, 0.5);
}

.reset-all:hover::before {
    opacity: 1;
}

.random-next {
    background: linear-gradient(60deg, rgba(33, 165, 185, 0.3) 0%, rgba(33, 165, 185, 0.5) 100%);
    color: white;
    border-color: rgba(33, 165, 185, 0.4);
}

.random-next::before {
    background: linear-gradient(60deg, rgba(33, 165, 185, 0.5) 0%, rgba(33, 165, 185, 0.7) 100%);
}

.random-next:hover {
    background: linear-gradient(60deg, rgba(33, 165, 185, 0.5) 0%, rgba(33, 165, 185, 0.7) 100%);
    border-color: rgba(33, 165, 185, 0.6);
    box-shadow: 0 15px 40px rgba(33, 165, 185, 0.5);
}

.random-next:hover::before {
    opacity: 1;
}

@media (max-width: 640px) {
    .quick-actions {
        padding: 20px;
    }

    .quick-actions h3 {
        font-size: 20px;
    }

    .action-btn {
        min-width: 100%;
        padding: 12px 24px;
    }
}
